@font-face {
    font-family: 'FuturaBookC';
    font-style: normal;
    font-weight: 400;
    src:
      local('FuturaBookC'),
      url(../fonts/FuturaBookC.woff2) format('woff2'),
      url(../fonts/FuturaBookC.woff) format('woff');
  }
  @font-face {
    font-family: 'NorthWellAlt';
    font-style: normal;
    font-weight: 400;
    src:
      local('NorthWellAlt'),
      url(../fonts/Northwell-Alt.woff2) format('woff2'),
      url(../fonts/Northwell-Alt.woff) format('woff');
  }
  
  @import url('https://fonts.googleapis.com/css?family=Cormorant+SC:300,400&display=swap');
  
  $color: #42261D;
  $colorHover: #B22D00;
  
  img {
    display: block;
    width: 100%;
  
    &:focus {
      outline: 0;
    }
  }
  
  ul {
    list-style: none;
    font-family: 'FuturaBookC';
    margin: 0;
  }
  
  a {
    color: $color;
    &:focus {
        outline: 0;
    }
  }
  a:hover{
    color: $colorHover;
    text-decoration: none;
  }
  
  p {
    margin: 0;
  }
  
  ul {
    margin: 0;
    padding: 0;
  }
  
  main {
    padding-top: 150px;
  }
  
  section {
    width: 100vw;
  }
  
  .viewport {
    margin: 0 0 0 0;
    width: 100vw;
    overflow-x: hidden;
    position: fixed;
  }

  .slick-slide:focus {
      outline: 0;
  }
  
  .sliderContainer {
    position: relative;
    .slick-arrow {
      position: absolute;
      z-index: 2;
      font-size: 0;
      padding: 0;
      border: 0;
      transition: all .3s ease;
      cursor: pointer;
  
      &:focus {
        outline: none;
      }
      svg {
        margin: auto;
        transition: all .3s ease;
      }
    }

  }
  
  
  body {
    scroll-behavior: smooth;
    margin: 0;
    padding: 0;
  }
  
  .buttons {
    display: flex;
  }
  
  .addToCart,
  .addToWish {
    background-image: url(../img/backgrounds/buttons.jpg);
    background-size: 100% 100%;
    height: 60px;
    cursor: pointer;
    font-family: 'FuturaBookC', Georgia, 'Times New Roman', Times, serif;
    font-size: 24px;
    color: #B22D00;
    letter-spacing: -0.03px;
    text-align: center;
    line-height: 60px;
    text-transform: uppercase;
    vertical-align: baseline;
    padding-top: 5px;
  
    span {
      position: relative;
    }
  }
  
  .addToWish {
    width: 30%;
  }
  .addToCart {
    width: 70%;
  
    img {
      width: 25px;
      display: inline-block;
    }
  }
  
  .price {
    font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
    font-size: 36px;
    color: #42261D;
    letter-spacing: 0;
    text-align: left;
    text-transform: uppercase;
    margin: 20px 0;
  
    span {
      padding-right: 30px;
    }
    span:nth-child(2) {
      opacity: .6;
      text-decoration: line-through;
    }
  }
  
  .sizeContainer {
    margin-bottom: 30px;
    position: relative;
  
    #sizeError {
      position: absolute;
      font-size: 18px;
      height: 0;
      opacity: 0;
      overflow: hidden;
      color: red;
      line-height: 24px;
      bottom: -60px;
      left: 5px;
      transition: all .3s ease;
    }
  
    #sizeError.show {
      height: auto;
      opacity: 1;
      bottom: -36px;
    }
  
    div:first-child {
      display: flex;
      justify-content: space-between;
      max-width: 310px;
  
      span:first-child {
        font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
        font-size: 16px;
        color: #B22D00;
        letter-spacing: 0;
        text-transform: uppercase;
      }
      span:last-child {
        font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
        font-size: 16px;
        color: #42261D;
        letter-spacing: 0;
        text-align: center;
  
        img {
          width: 15px;
          display: inline-block;
          margin-right: 10px;
        }
      }
    }
    .chooseSize {
      display: flex;
      width: 100%;
      max-width: 310px;
  
      span {
        width: 100%;
        text-transform: uppercase;
        font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
        font-size: 16px;
        color: #42261D;
        letter-spacing: 0;
        text-align: center;
        line-height: 40px;
        border: 1px solid white;
        padding-top: 5px;
        cursor: pointer;
        background-color: rgba(255, 232, 223, .7);
      }
      span.selected {
        background-image: url(../img/icons/sizeSelected.jpg);
        color: white;
      }
    }
  }
  
  .titlePage {
    background-image: url(../img/backgrounds/titlePage.jpg);
    background-size: 100%;
    position: fixed;
    z-index: 1;
    top: 150px;
    width: 100vw;
  
    h3 {
      font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
      font-size: 30px;
      color: #B22D00;
      letter-spacing: -0.05px;
      text-align: center;
      text-transform: uppercase;
      line-height: 1.7;
      box-sizing: content-box;
      vertical-align: middle;
      margin: 0;
      padding-top: 5px;
    }
  }
  
  .mainSlider {
    width: 100%;
    margin-left: auto;
    margin-right: 0;
    .slick-slide {
      padding-bottom: 40px;
    }
    .slick-slide img {
      margin-left: auto;
      margin-right: 0;
      width: 95%;
    }
    .slick-list {
      box-sizing: content-box;
    }
    .slick-dots {
      display: flex;
      flex-direction: column;
      top: 0;
      left: 0;
      width: 100px;
      height: 100%;
      overflow-y: scroll;
      position: absolute;
      
      &::-webkit-scrollbar {
        display: none;
      }
  
      li {
        display: block;
        width: 100px;
        height: auto;
        margin: 0;
        padding: 0;
        margin-bottom: 20px;
        opacity: 0.4;
      }
      li.slick-active {
        opacity: 1;
      }
    }
    
  }
  
  .checkContainer {
    display: block;
    position: relative;
    padding-left: 25px;
    cursor: pointer !important; 
    z-index: 1;
  
    input {
      position: absolute;
      height: 100%;
      width: 100%;
      opacity: 0;
      top: 0;
      left: 0;
      z-index: -1;
    }
    .check {
      position: absolute;
      left: 0;
      top: calc(50% - 10px);
      border: 1px solid #42261D;
      width: 16px;
      height: 16px;
  
      &:after {
        content: "";
        position: absolute;
        top: 2px;
        left: 2px;
        width: 10px;
        height: 10px;
        background: #FFE8DF;
        border: 1px solid #42261D;
        display: none;
      }
    }
    input:checked ~ .check:after {
      display: block;
    }
    &:hover .check{
      outline: 1px solid #42261D;
    }
  }
  
  .oneProduct {
    position: relative;
  
    .addToWish {
      width: 30px;
      height: 30px;
      background: none;
      position: absolute;
      line-height: 30px;
      padding-top: 0;
      z-index: 3;
      top: 15px;
      left: 15px;
      background-image: url(../img/backgrounds/wishIconBack.svg);
      background-repeat: no-repeat;
      background-size: 100%;
    }
    .addToWish.addedToWish{
      background-image: url(../img/backgrounds/wishIconAdded.svg);
    }
    a {
      display: block;
      font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
      font-size: 24px;
      color: #42261D;
      letter-spacing: 0;
      text-align: center;
      line-height: 1.2;
      margin-top: 10px;
    }
    .price {
      font-size: 24px;
      margin: 0;
      line-height: 1.2;
      text-align: center;
      width: 100%;
    }
    .imgBlock {
      display: block;
      overflow: hidden;
    }
    img {
      transition: transform 2s ease;
    }
    &:hover {
      a, .price {
        color: #B22D00;
      }
      img {
        transform: scale(1.1);
      }
    }
  }
  
  .additionalSlide {
    padding: 50px 0;
    padding-bottom: 200px;
    background-image: url(../img/backgrounds/additionalSlider.jpg);
    background-size: 100%;
    background-repeat: repeat-y;
  
    .title {
      font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
      font-size: 50px;
      color: #B22D00;
      letter-spacing: 0;
      text-transform: uppercase;
      line-height: 1.5;
    }
    .slick-slide {
  
      .imgBlock {
        width: 90%;
        margin: auto;
      }
      .addToWish {
        top: 20px;
        left: 40px;
      }
    }
    .slick-list {
      padding: 0 !important;
    }
    .slick-arrow {
      border: 1px solid #B22D00;
      width: 34px;
      height: 46px;
      top: -56px;
      transform: none;
  
      svg {
        width: 20px;
      }
      #slider-arrow {
        fill: #B22D00;
      }
      &:active svg {
        width: 15px;
      }
    }
    .slick-prev {
      right: 50px;
      left: auto;
    }
    .slick-next {
      right: 18px;
      transform: rotate(180deg);
    }
  }
  
  .outlet, .promo, .wishContent {
    padding-top: 250px;
    background-image: url(../img/backgrounds/outlet.jpg);
    background-size: 100%;
    background-repeat: repeat-y;
  
    .additionalSlide {
      margin-top: 80px;
    }
    .oneProduct {
      margin-bottom: 40px;

      .remove {
        position: absolute;
        z-index: 3;
        top: 15px;
        left: 15px;
        width: 30px;
        height: 30px;
        background-image: url(../img/icons/plusIconBlack.svg);
        background-size: 20px 20px;
        background-position: center center;
        background-repeat: no-repeat;
        cursor: pointer;
        transform: rotate(45deg);
        cursor: pointer;

        &:active {
          background-size: 15px;
        }
      }
    }

  }
  
  .promo {
    padding-top: 200px;
  
    .promoTitle {
      font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
      font-size: 36px;
      color: #B22D00;
      letter-spacing: 0;
      text-transform: uppercase;
    }
    .promoInner {
      padding: 80px 0;
    }
  }
  
  
  .selectContainer {
    position: relative;
    width: 100%;
    border: 1px solid #42261D;
    margin: 5px 0;
  
    select {
      -webkit-appearance: none;
      -moz-appearance:    none;
      appearance:         none;
      background: none;
      border: 0;
      cursor: pointer;
      font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
      font-size: 18px;
      color: #42261D;
      letter-spacing: 0;
      line-height: 30px;
      height: 30px;
      padding-left: 15px;
      width: 100%;
  
      &:focus {
        outline: 0;
      }
    }
    span {
      position: absolute;
      right: 15px;
    }
  }
  
  input, textarea {
    font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
    background: none;
    outline: 0;
    font-size: 16px;
    line-height: 30px;
    height: 30px;
    border: 1px solid #42261D;
    width: 100%;
    padding-left: 15px;
  }
  
  button {
    &:focus {
      outline: none;
    }
  }
  
  .wishContent {
    padding-top: 150px;
    padding-bottom: 80px;
  
    h3 {
      font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
      font-size: 50px;
      color: #B22D00;
      letter-spacing: -0.05px;
      text-align: center;
      text-transform: uppercase;
      line-height: 3;
      box-sizing: content-box;
      vertical-align: middle;
      margin: 0;
      padding-top: 5px;
    }
    .addToCart {
      width: 100%;
      margin: 0;
      margin-top: 10px;
  
      svg {
        margin-left: 5px;
        margin-bottom: 10px;
  
        path:last-child {
          fill: #B22D00;
        }
      }
    }
    .oneProduct {
      margin-bottom: 30px;
    }
  }
  
  .notFoundContent {
    padding-top: 150px;
    background-color: #FDF9F6;
    width: 100vw;
    height: 100vh;
    background-image: url(../img/backgrounds/notFound1.png), url(../img/backgrounds/notFound2.png);
    background-position: left bottom, right 150px;
    background-size: 35vw, 30vw;
    background-repeat: no-repeat;
  
    a {
      font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
      font-size: 24px;
      color: #42261D;
      letter-spacing: 0;
      text-align: center;
      text-transform: uppercase;
    }
    .title {
      font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
      font-size: 30vh;
      color: #B22D00;
      text-align: left;
      line-height: 1.2;
    }
    p{
      font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
      font-size: 24px;
      color: #42261D;
      text-align: center;
      text-shadow: 0 30px 50px rgba(0,0,0,0.20);
      line-height: 1.2;
  
      &:first-child {
        font-size: 50px;
        margin-bottom: 40px;
      }
      &:nth-child(2) {
        width: 60%;
        margin: auto;
        margin-bottom: 40px;
      }
    }
    .buttons {
      a {
        display: block;
        background-image: url(../img/backgrounds/buttons.jpg);
        background-size: 100% 100%;
        width: 300px;
        line-height: 50px;
        height: 50px;
        padding-top: 3px;
        margin-right: 30px;
        margin-top: 40px;
  
        &:first-child {
          background-image: url(../img/backgrounds/buttonBrown.jpg);
        }
      }
    }
  }
  
  .butt {
    background: #C39A7A;
    background-image: url(../img/backgrounds/buttons.jpg);
    background-size: 100% 100%;
    margin-top: 30px;
    border: 1px solid #FFFFFF;
    display: block;
    width: 440px;
    text-align: center;
    padding-top: 6px;
    overflow: hidden;
    
    span {
      font-family: FuturaBookC, Georgia, 'Times New Roman', Times, serif;
      font-size: 24px;
      color: #FFFFFF;
      letter-spacing: 0;
      line-height: 2.5;
      text-align: center;
      text-transform: uppercase;
      position: relative;
      vertical-align: bottom;
      padding-right: 49px;
      transition: padding .3s ease-out;
    }
    b {
      border: 3px solid #fff;
      line-height: 0px;
      font-size: 0;
      border-radius: 50%;
      display: inline;
      position: absolute;
      right: 0;
      top: calc(50% - 5px);
      opacity: 1;
      transition: right .3s ease-out;
    }
    b:nth-child(1) {
      right: 0px;
    }
    b:nth-child(2) {
      right: 15px;
    }
    b:nth-child(3) {
      right: 30px;
    }
    &:hover b:nth-child(1) {
      right: 0px;
    }
    &:hover b:nth-child(2) {
      right: 0;
    }
    &:hover b:nth-child(3) {
      right: 0;
    }
    &:hover span {
      padding-right: 15px;
    }
  }
  
  @keyframes showfixed {
    0% {opacity: 0;}
    49.99% {opacity: 0;}
    50% {opacity: 1;}
    100% {opacity: 1;}
  }
  
  @media (max-width: 1100px) {
    .container {
      max-width: 95vw;
    }
    main {
      padding-top: 130px;
    }
    .price {
      font-size: 22px;
    }
    .titlePage {
      top: 130px;
    }
  }
  @media (max-width: 992px) {
    .tabletNone {
      display: none;
    }
  }


// HEADER //

header {
    height: 150px;
    width: 100vw;
    background-color: #FFE7DD;
    background-image: url(../img/backgrounds/header-background.jpg);
    background-size: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 5;
  
    a {
      text-transform: uppercase;
      padding: 0 15px;
    }
  }
  
  .headerTop {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 90px;
    .logo {
      display: flex;
      justify-content: center;
      align-items: center;
  
      img{
        width: 200px; 
      }
    }
  
    a {
      font-family: 'FuturaBookC', Georgia, 'Times New Roman', Times, serif;
      font-size: 20px;
      letter-spacing: 0;
      text-align: center;
    }
  
    li {
      float: left;
    }
  }
  
  .headerBottom {
    display: flex;
    width: 100vw;
    height: 60px;
    left: 0;
  
    .utilBloc{
      background-color: rgba(255, 231, 221, 0.8);
      height: 60px;
      width: 100%;
    }
  
    ul {
      display: flex;
      justify-content: center;
    }
  
    span {
      display: block;
      position: relative;
      overflow: hidden;
  
      &:after {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background-color: rgba(255, 231, 221, 0.8);
        z-index: 1;
        transition: top .7s ease;
      }
  
      &:hover {
        &:after {
          top: 60px;
        }
      }
    }
  
    a{
      line-height: 60px;
      text-align: center;
      position: relative;
      z-index: 2;
      font-size: 14px;
      white-space: nowrap;
      width: 100%;
      display: block;
    }
  }
  
  .userSettings {
  
    a {
      position: relative;
      display: block;
      text-align: center;
      padding: 0;
    }
  
    li {
      padding: 0 15px;
    }
  
    #wish svg,
    #cart svg {
      position: absolute;
      display: block;
      width: 100%;
    }
  
    span {
      position: relative;
      z-index: 2;
      font-size: 14px;
      line-height: 26px;
      display: block;
      padding-top: 2px;
    }
  
    #wish {
      width: 35px;
      padding-top: 3px;
    }
  
    #cart {
      width: 26px;
      margin-top: -3px;
      padding-top: 5px;
  
      svg{
        margin-top: -5px;
      }
    }
  
    #settings {
      display: flex;
      white-space: nowrap;
      margin-top: 2px;
      
      span {
        line-height: 26px;
        font-size: 20px;
        &:last-child {
          width: 26px;
        }
      }
      img {
        margin-top: 3px;
        margin-left: 3px;
      }
    }
  
    #avatar {
      width: 28px;
  
      svg {
        margin-bottom: 0px;
      }
    }
    .logged circle {
      fill: #FFE8DF;
    }
    .logged #Oval-Copy {
      fill: #FFE8DF;
    }
  }
  
  .submenu {
    position: relative;
  
    .firstLvl {
      position: absolute;
      background-color: #FFE8DF;
      background-image: url(../img/backgrounds/submenu.jpg);
      box-shadow: 0 7px 11px 0 rgba(0,0,0,0.28);
      padding: 15px 0;
      opacity: 0;
      transition: opacity .3s ease;
      z-index: -1;
      height: 0;
      overflow: hidden;
  
      a {
        white-space: nowrap;
        font-size: 16px;
        line-height: 40px;
        display: block;
        text-align: left;
      }
      li:hover > a {
        color: #B22D00;
      }
      
    }
  
    li {
      display: block;
      float: none;
      padding-right: 30px;
      padding-left: 15px;
      line-height: 40px;
  
      &:hover {
        background-color: #FFE8DF;
      }
    }
  
    .nextLvl {
      position: absolute;
      background-image: url(../../img/backgrounds/nextLvl.jpg);
      background-size: cover;
      background-position: 0;
      box-shadow: 0 7px 11px 0 rgba(0,0,0,0.28);
      left: 100%;
      top: 0;
      padding-top: 15px;
      min-height: 100%;
      min-width: 110%;
      opacity: 0;
      z-index: -1;
      transition: opacity .3s ease;
  
      li:hover {
        background-color: transparent;
      }
    }
  
    &:hover > span a {
      color: #B22D00;
    }
    
    .onHover:hover > .nextLvl {
      opacity: 1;
      z-index: 3;
    }
  }
  
  .submenu.hoverThis:hover > .firstLvl {
    z-index: 3;
    opacity: 1;
    height: auto;
    overflow: visible;
  }
  
  @media (max-width: 1100px) {
    header {
      height: 130px;
  
      a {
        padding: 0 5px;
      }
    }
    .headerTop {
      height: 70px;
  
      a{
        font-size: 16px;
      }
      .logo img {
        width: 100px;
      }
    }
  }
  
// HOME PAGE //

.slideHome {

    .slick-arrow {
      width: 70px;
      height: 70px;
      top: calc(50% - 35px);
      display: flex;
      justify-content: center;
      align-items: center;
      &::before{
        display: none;
      }
      svg {
        width: 35px;
      }
      &:active svg{
        width: 25px;
      }
    }
    .slick-arrow:hover {
      &:before {
        display: none;
        left: 0;
      }
    }
    .slick-next {
      right: 30px;
      transform: rotate(180deg);
    }
    .slick-prev {
      left: 30px;
      top: 50%;
    }
    .item {
      position: relative;
      display: block;
  
      &:focus {
        outline: none;
      }
    }
    .text {
      position: absolute;
      left: 10vw;
      top: calc(50% - 95px);
      font-size: 50px;
      line-height: .7;
      font-family: "Cormorant SC", Georgia, 'Times New Roman', Times, serif;
      text-align: center;
