      letter-spacing: 0.13px;
      line-height: 35px;
    }
    .top {
      position: relative;
      margin-right: -150px;
      z-index: 2;
      top: -500px;

      img {
        position: relative;
        transition: top .5s ease;
      }
    }
    .bottom {
      position: relative;
      margin-left: -100px;
      z-index: 2;
      top: -460px;
      margin-bottom: 80px;
      
      img {
        position: relative;
        transition: top 1s ease
      }
    }
    .scaleOut {
      overflow: hidden;
  
      img {
        transition: transform 1s ease;
      }
    }
    p.subtext{
      margin-top: 80px;

      img {
        width: 70%;
      }
    }
    .imgContainer {
      z-index: 1;
    }
    .imgContainer .crumbsabout {
      position: absolute;
      bottom: -40px;
      left: -50%;
      right: auto;
      width: 150%;
      z-index: -1;
    }
    .welcome {
      width: 800px;
      margin: auto;
      margin-top: 100px;
    }
  }
}

@media (max-width: 1201px) {
  .aboutContent {
    .container {
      max-width: 95vw;
    }
    section:nth-child(4) .imgContainer {
      height: 500px;
    }
  }
}


